import React from 'react'
import Header from '../header/Header'
import BackgroundContainer from '../BackgroundContainer/BackgroundContainer'
import { connect } from 'react-redux'
import DatePicker from 'react-datepicker'
import 'react-datepicker/dist/react-datepicker.css'
import styles from './CreateTrip.module.css'
import { changeTripName, changeTripEndDate, changeTripStartDate } from '../../actions/changeCreateTripDetails'

function CreateTrip (props) {
  const {
    tripName,
    startDate,
    endDate
  } = props.createTripData
  const { changeTripName, changeTripEndDate, changeTripStartDate } = props

  return (
    <>
      <Header />
      <BackgroundContainer>
        <div className={styles.container}>
          <h2>Trip Name</h2>
          <input value={tripName} onChange={changeTripName} />
          <div>
            <h2>From</h2>
            <DatePicker selected={startDate} onChange={changeTripStartDate} />
            <h2>To</h2>
            <DatePicker selected={endDate} onChange={changeTripEndDate} />
          </div>
          <button>SUBMIT</button>
        </div>
      </BackgroundContainer>
    </>
  )
}
function mapStateToProps (state) {
  return {
    createTripData: state.createTripData
  }
}

function mapDispatchToProps (dispatch) {
  return {
    changeTripName: e => {
      dispatch(changeTripName(e))
    },
    changeTripStartDate: date => {
      dispatch(changeTripStartDate(date))
    },
    changeTripEndDate: date => {
      dispatch(changeTripEndDate(date))
    }
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(CreateTrip)
