import React from 'react'
import LogoHeader from '../logoHeader/LogoHeader'
import { Formik, Form } from 'formik'
import * as Yup from 'yup'
import styles from '../Login/Login.module.css'
import InputLabelError from '../inputLabelError/InputLabelError'
import { withRouter } from 'react-router-dom'
import { API_URL } from '../../api'

function Register (props) {
  return (
    <>
      <LogoHeader />
      <div className={styles['form-box']}>
        <div className={styles['form-inner-box']}>
          <p className={styles['form-title']}>Register</p>
          <Formik
            validationSchema={Yup.object().shape({
              password: Yup.string()
                .min(8, 'Password is too small')
                .max(13)
                .required('Password required'),
              email: Yup.string()
                .email('Email must be of valid format')
                .required('Email required'),
              name: Yup.string().required('Name required')
            })}
            onSubmit={async (values, { setSubmitting }) => {
              // console.log(values);
              // setTimeout(() => {
              //   alert(JSON.stringify(values, null, 2))
              //   setSubmitting(false)
              // }, 400)
              const response = await fetch(`${API_URL}/register`, {
                method: 'POST',
                headers: {
                  'Content-Type': 'application/json'
                },
                body: JSON.stringify(values)
              })

              const responseData = await response.json()
              
              if (response.status===201) {
                props.history.push('/login')
              }
            }}
            initialValues={{ password: '', name: '', email: '' }}
          >
            {({ isSubmitting }) => {
              return (
                <Form>
                  <InputLabelError
                    inputName='name'
                    inputType='text'
                    inputLabel='Name'
                  />
                  <InputLabelError
                    inputName='email'
                    inputType='email'
                    inputLabel='Email'
                  />
                  <InputLabelError
                    inputName='password'
                    inputType='password'
                    inputLabel='Password'
                  />

                  <button
                    type='submit'
                    className={styles['form-submit-button']}
                    disabled={isSubmitting}
                  >
                    Submit
                  </button>
                </Form>
              )
            }}
          </Formik>
        </div>
      </div>
    </>
  )
}

export default withRouter(Register)
