import React from 'react'
import SideBar from '../SideBar/SideBar'
import Content from '../Content/Content'
import { connect } from 'react-redux'
import styles from './UserDashBoard.module.css'
import setTripObject from '../../actions/setTripObject'
import getItineraryData from '../../actions/getItineraryData'
import { withRouter } from 'react-router-dom'
import { CHANGE_ITINERARY_SELECT_OPTION, CHANGE_TAB } from '../../actions/actionConstants'

const mapDispatchToProps = dispatch => {
  return {
    setTripObject: tripId => {
      dispatch(setTripObject(tripId))
    },
    getItineraryData: tripId => {
      dispatch(getItineraryData(tripId))
    },
    changeMainTab: ({ tripId, tabName }) => {
      dispatch({
        type: CHANGE_TAB,
        payload: {
          id: tripId,
          currentTab: tabName
        }
      })
    },
    changeItinerarySelectOption: ({ selectedOption, id }) => {
      dispatch({
        type: CHANGE_ITINERARY_SELECT_OPTION,
        payload: {
          selectedOption,
          id
        }
      })
    }
  }
}

const mapStateToProps = state => ({
  tripData: state.tripData
})

class UserDashBoard extends React.Component {
  componentDidMount () {
    // let itinerary component fetch the admin details
    this.props.setTripObject(this.props.match.params.id)
  }

  filterTripData () {
    const { id } = this.props.match.params
    const actualTripObjectArray = this.props.tripData.filter((tripObj) => {
      if (tripObj.id === id) { return true }
      return false
    })
    return actualTripObjectArray[0]
    // returns an object or undefined
  }

  render () {
    const { getItineraryData, changeItinerarySelectOption, changeMainTab } = this.props

    return (
      <div className={styles.container}>
        <SideBar
          changeMainTab={changeMainTab}
          tripData={this.filterTripData()}
        />
        <Content
          getItineraryData={getItineraryData}
          changeItinerarySelectOption={changeItinerarySelectOption}
          tripData={this.filterTripData()}
        />
      </div>
    )
  }
}

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(UserDashBoard)
)
