import React from 'react'
import Select from 'react-select'
import styles from './ItineraryDisplay.module.css'

const customSelectStyles = {
  option: (provided, state) => ({
    ...provided,
    borderBottom: '1px solid #222',
    color: state.isSelected ? 'black' : '#222',
    padding: 10,
    fontSize: '2rem',
    fontFamily: "'Roboto', sans-serif"
  }),

  singleValue: (provided, state) => {
    const opacity = state.isDisabled ? 0.5 : 1
    const transition = 'opacity 300ms'
    const fontSize = '2rem'
    const fontFamily = "'Roboto', sans-serif"
    return { ...provided, opacity, transition, fontSize, fontFamily }
  }
}

class ItineraryDisplay extends React.Component {
  componentDidMount () {
    const { getItineraryData, tripId } = this.props
    getItineraryData(tripId)
  }

  getSelectOptions () {
    const { data } = this.props.itinerary
    const itineraryDays = data.map(dayObj => {
      const dayData = `Day ${dayObj.day}`
      return {
        value: dayObj.day,
        label: dayData
      }
    })
    return itineraryDays
  }

  renderSelectedDayDetails () {
    const { data, selectedOption } = this.props.itinerary
    const tripObj = data.filter(ItineraryDayData => {
      if (ItineraryDayData.day === selectedOption.value) {
        return true
      }
      return false
    })
    const { location, activity, date } = tripObj[0]
    return (
      <>
        <div className={styles.dateplace}>
          <span className={styles.date}>{date}</span>&#183;
          <span className={styles.place}>{location}</span>
        </div>
        <ul className={styles['activity_container']}>
          {activity.map(activityObj => {
            return (
              <li key={activityObj._id} className={styles['activity_item']}>
                {activityObj.task}
              </li>
            )
          })}
        </ul>
      </>
    )
  }

  render () {
    const { isLoading, data, error, selectedOption } = this.props.itinerary
    return (
      <>
        {isLoading ? (
          <>
            <div className={`loadingDiv ${styles.select_loader}`} />
            <div className={`loadingDiv ${styles.block}`} />
            <div className={`loadingDiv ${styles.block}`} />
            <div className={`loadingDiv ${styles.block}`} />
          </>
        ) : (
          <>
            <div className={styles.select_container}>
              <Select
                styles={customSelectStyles}
                options={this.getSelectOptions()}
                value={selectedOption}
                onChange={selectedOption => {
                  console.log(selectedOption)
                  this.props.changeItinerarySelectOption({
                    selectedOption,
                    id: this.props.tripId
                  })
                }}
              />
            </div>
            {this.renderSelectedDayDetails()}
          </>
        )}
      </>
    )
  }
}

export default ItineraryDisplay
