import React from 'react'
import Header from '../header/Header'
import BackgroundContainer from '../BackgroundContainer/BackgroundContainer'
import ItineraryDisplay from '../Itinerary/ItineraryDisplay'
import TodoApp from '../Todo/TodoApp'
import styles from './Content.module.css'

class Content extends React.Component {
  renderCorrectTab () {
    const {
      tripData,
      getItineraryData,
      changeItinerarySelectOption
    } = this.props

    if (!tripData) return null
    const { currentTab } = tripData
    switch (currentTab) {
      case 'chat': return <div>Chat</div>
      case 'todo': return (
        <TodoApp
          todo={tripData.todo}
          tripId={tripData.id}
        />
      )
      case 'itinerary': return (
        <ItineraryDisplay
          itinerary={
            tripData.itinerary
          }
          tripId={tripData.id}
          getItineraryData={
            getItineraryData
          }
          changeItinerarySelectOption={
            changeItinerarySelectOption
          }
        />
      )
      default: return null
    }
  }

  render () {
    return (
      <div>
        <Header />
        <BackgroundContainer>
          <main className={styles.container}>
            {this.renderCorrectTab()}
          </main>
        </BackgroundContainer>
      </div>

    )
  }
}

export default Content
