import {
  CHANGE_CREATE_TRIP_START_DATE,
  CHANGE_CREATE_TRIP_END_DATE,
  CHANGE_CREATE_TRIP_NAME
} from './actionConstants'

export const changeTripName = (event) => {
  return {
    type: CHANGE_CREATE_TRIP_NAME,
    payload: {
      tripName: event.target.value
    }
  }
}

export const changeTripStartDate = (date) => {
  return {
    type: CHANGE_CREATE_TRIP_START_DATE,
    payload: {
      startDate: date
    }
  }
}

export const changeTripEndDate = (date) => {
  return {
    type: CHANGE_CREATE_TRIP_END_DATE,
    payload: {
      endDate: date
    }
  }
}
