import {
  CHANGE_CREATE_TRIP_START_DATE,
  CHANGE_CREATE_TRIP_END_DATE,
  CHANGE_CREATE_TRIP_NAME
} from '../actions/actionConstants'

const initialCreateTripData = {
  tripName: '',
  startDate: new Date(),
  endDate: new Date()
}

export default (state = initialCreateTripData, action) => {
  switch (action.type) {
    case CHANGE_CREATE_TRIP_NAME:
      return {
        ...state,
        tripName: action.payload.tripName
      }
    case CHANGE_CREATE_TRIP_START_DATE:
      return {
        ...state,
        startDate: action.payload.startDate
      }
    case CHANGE_CREATE_TRIP_END_DATE:
      return {
        ...state,
        endDate: action.payload.endDate
      }

    default:
      return state
  }
}
