import {
  CHANGE_TAB,
  SET_TRIP_OBJECT,
  GET_ITINERARY_DATA_START,
  GET_ITINERARY_DATA_FAIL,
  GET_ITINERARY_DATA_SUCCESS,
  CHANGE_ITINERARY_SELECT_OPTION,
  CREATE_TODO_ITEM_START,
  CREATE_TODO_ITEM_FAIL,
  CREATE_TODO_ITEM_SUCCESS
} from '../actions/actionConstants'

const tripDataReducer = (state = [], action) => {
  switch (action.type) {
    case CHANGE_TAB: {
      const newState = state.map(trip => {
        if (trip.id !== action.payload.id) {
          return trip
        }
        return {
          ...trip,
          currentTab: action.payload.currentTab
        }
      })
      return newState
    }
    case SET_TRIP_OBJECT:
      let tripObjectPresent = false
      for (const tripObj of state) {
        if (tripObj.id === action.payload.id) {
          tripObjectPresent = true
        }
      }
      if (tripObjectPresent) {
        return state
      }
      return [
        ...state,
        {
          id: action.payload.id,
          todo: {
            error: {
              createTodoError:null,
              fetchingDataError:null
            },
            isLoading: true,
            data: null,
          },
          chat: null,
          itinerary: {
            error: null,
            isLoading: true,
            data: null,
            selectedOption: {
              label: 'Day 1',
              value: 1
            }
          },
          currentTab: 'itinerary',
          isAdmin: null
        }
      ]
    case GET_ITINERARY_DATA_START: {
      const newState = state.map(trip => {
        if (trip.id !== action.payload.id) {
          return trip
        }
        return {
          ...trip,
          itinerary: { ...trip.itinerary, isLoading: true }
        }
      })
      return newState
    }

    case GET_ITINERARY_DATA_SUCCESS: {
      const newState = state.map(trip => {
        if (trip.id !== action.payload.id) {
          return trip
        }
        return {
          ...trip,
          itinerary: {
            ...trip.itinerary,
            data: action.payload.data.itinerary,
            isLoading: false
          }
        }
      })
      return newState
    }

    case GET_ITINERARY_DATA_FAIL: {
      const newState = state.map(trip => {
        if (trip.id !== action.payload.id) {
          return trip
        }
        return {
          ...trip,
          itinerary: {
            ...trip.itinerary,
            error: action.payload.error,
            isLoading: false
          }
        }
      })
      return newState
    }
    case CHANGE_ITINERARY_SELECT_OPTION: {
      const newState = state.map(trip => {
        if (trip.id !== action.payload.id) {
          return trip
        }
        return {
          ...trip,
          itinerary: { ...trip.itinerary, selectedOption: action.payload.selectedOption }
        }
      })
      return newState
    }

    case CREATE_TODO_ITEM_START: {
      const newState = state.map(trip => {
        if (trip.id !== action.payload.tripId) {
          return trip
        }
        return {
          ...trip,
          todo: [{
            completed: false,
            id: action.payload.todoItemId,
            text: action.payload.text,
            note: '',
            createdAt: action.payload.createdAt
          }, ...trip.todo]

        }
      })
      return newState
    }

    default:
      return state
  }
}

export default tripDataReducer
